import React,{useEffect, useState} from 'react';
import styled from 'styled-components';
import {Link, NavLink} from 'react-router-dom';

const Wrapper = styled.div`
    position:fixed;
    transition:all ease .3s;
    top:0;
    left:0;
    width:100%;
    right: 0;
    height: fit-content;
    z-index:1000;
    // .container{
    //     padding:20px 0;
    // }
    .logo{
        width:114px;
    }
    .header-content{
        height:80px;
    }
    .nav-menu{
        li{
            list-style:none;
            a{
                font-weight: 400;
                font-size: 18px;
                line-height: 27px;
                color: #FFFFFF;
            }
            .active{
                font-weight: 400;
            }

        }
        .get-started{
            a{
                p{
                    background: #24D0F6;
                    border-radius: 8px;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 27px;
                    color:#fff;
                    width:fit-content;
                    white-space:nowrap;
                }
            }
        }
        
    }
    
    @media (max-width:991px){
        // .container{
        //     padding:17px 0;
        // }
        .nav-bar{
            background:#1E0C2D;
            position:fixed;
            top:0;
            left:-100%;
            height:100vh;
            width:50%;
            transition:all ease .3s;
        }
        .nav-bar.show{
            left:0;
        }
    }
    @media (max-width:767px){
        .nav-bar{
            width:70%;
        } 
    }
`;
export default function Index({half}) {
    const [sticky, setSticky] = useState(false)
    const [showNav, setShowNav] = useState(false)
    window.addEventListener('scroll', () =>{
        if(window.pageYOffset > "50"){
            setSticky(true)
        }else{
            setSticky(false)
        }
    })

    useEffect(() =>{

    },[])

    return (
        <Wrapper >
            <div className=" header-content align-items-center d-flex" style={{background:sticky ?" rgba(30,12,45,.98)" :""}}>
                <div className="container  w-100">
                    <div className="row align-items-center">
                        <div className="col-xl-6 col-lg-5  col-6">
                            <Link to="/">
                                <div className="d-flex">
                                    <img src={require('../../assets/images/logo.svg')} alt="" className="logo"/>
                                </div>
                            </Link>
                        </div>
                        
                        <div className={`col-xl-6 col-lg-7 pr-0 nav-bar ${showNav ? 'show' : ''}`}>
                            <div className="d-lg-flex align-items-center justify-content-between nav-menu pt-5 pt-lg-0 px-lg-0 px-3">
                                <ul className="m-0 p-0 d-lg-flex">
                                    <li className="mr-5 mb-3 mb-lg-0" ><NavLink to="/" activeClassName="active">Home</NavLink></li>
                                    <li className="pl-1 mr-5 mb-3  mb-lg-0"><NavLink to="/about">About Us</NavLink></li>
                                    <li className="pl-1 mb-3  mb-lg-0"><NavLink to="/faq">FAQs</NavLink></li>
                                </ul>
                                <div className="get-started">
                                    <a href=""><p className="mb-0 py-2 px-5">Get Started</p></a>
                                </div>
                            </div>
                            
                        </div>

                        <div className={`col-6 d-lg-none d-flex justify-content-end`}>
                            
                            {showNav 
                            ? <img src={require(`../../assets/images/cancel.svg`)} alt="" style={{width:"25px"}} onClick={() => setShowNav(!showNav)}/>
                            : <img src={require(`../../assets/images/menu.svg`)} alt="" style={{width:"35px"}} onClick={() => setShowNav(!showNav)}/>}
                        </div>
                    </div>
                </div>
                
            </div>
        </Wrapper>
    )
}
