import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom'
const Wrapper = styled.footer`
    padding:80px 0px 40px 0;
    background-color: #1E0C2D;
    // mix-blend-mode: multiply;
    background-blend-mode: multiply;
    // background:#1e2531;
    background-size:cover;
    background-position:center;
    position:relative;

    .container{
        z-index:5;
        position:relative;
    }

    .overlay{
        position:absolute;
        top:0;
        right:0;
        left:0;
        bottom:0;
        opacity:.1;
    }
   

    .logo{
        margin-bottom:40px;
    }
    .social{
        width:fit-content;
        img{
            width:26px;
        }
        position:relative;
    }
    li{
        list-style:none;
    }
    .footer-link{
        // width:fit-content;
        margin-bottom:80px;
        .mr-5{
            margin-right:100px !important;
        }
        li{
            font-family: Roboto;
            color:#fff;
            h6{
                font-family: Poppins;
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 30px;
                color: #FFFFFF;
                white-space:nowrap;
            }   
            .item{
                font-family: Poppins;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 24px;
                margin-bottom:5px;
                
            }
            a{
                color: #E6E6E6;
            }
            
        }
    }
.copyright{
    font-family: Poppins;
    font-weight: normal;
    font-size: 16px;
    line-height: 30px;
    color: #FFFFFF;
}
@media (max-width:500px){
    .footer-link{
        margin-bottom:50px;
    }
    .social{
        padding-bottom:60px;
    }
    h6{
        line-height:18px !important;
    }
}
`;


export default function Index() {
    return (
        <Wrapper style={{backgroundImage:`linear-gradient(rgba(31,9,48,0.3), rgba(31,9,48,.3)), url(${require(`../../assets/images/footer-bg.jpg`)})`}}>
            <div className="container">

                <div className="row">
                    <div className="col-lg-8 mx-auto pl-md-5">
                        <ul className="row footer-link p-0 pl-md-5">
                            <li className="col-md-4 col-6 mb-4 mb-md-0">
                                <h6 className="mb-4">About us</h6>
                                <ul className="pl-0">
                                    <li className="item"><Link to="/about">About us</Link></li>
                                    <li className="item"><Link to="">Career</Link></li>
                                    <li className="item"><Link to="">Partnerships</Link></li>
                                    <li className="item"><Link to="">Press</Link></li>
                                </ul>
                            </li>
                            <li className="col-md-4 col-6 mb-4 mb-md-0 ml-n3">
                                <h6 className="mb-4">Help &amp; Support</h6>
                                <ul className="pl-0">
                                    <li className="item"><Link to="/faq">FAQ</Link></li>
                                    <li className="item"><Link to="">Terms of Service</Link></li>
                                    <li className="item"><Link to="">Contact us</Link></li>
                                    <li className="item"><Link to="">Privacy Policy</Link></li>
                                    <li className="item"><Link to="">Cookies</Link></li>
                                </ul>
                            </li>
                            
                            <li className="col-md-4">
                                <h6 className="mb-4">SUPPORT</h6>
                                <ul className="pl-0">
                                    <li className="item"><Link to="">Blog</Link></li>
                                    <li className="item"><a href="" target="_blank">Facebook</a></li>
                                    <li className="item"><a href="" target="_blank">Twitter</a></li>
                                    <li className="item"><a href="" target="_blank">Youtube</a></li>
                                </ul>
                            </li>
                        </ul>
                    </div>
                </div>

                <ul className="d-flex social mx-auto pl-0 mb-3 m-0">
                    <li className="mr-3"><Link to=""><img src={require('../../assets/images/facebook.svg')} alt="" className="mx-auto d-block"/></Link></li>
                    <li className="mr-3"><Link to=""><img src={require('../../assets/images/twitter.svg')} alt="" className="mx-auto d-block"/></Link></li>
                    <li className=""><Link to=""><img src={require('../../assets/images/instagram.svg')} alt="" className="mx-auto d-block"/></Link></li>
                </ul>

                <p className="copyright mb-0 text-center">Copyright Qiiper 2020</p>
            </div>
            {/* <div className="overlay" style={{backgroundImage:`url(${require('../../assets/img/footer.svg')})`}}></div> */}

        </Wrapper>
    )
}
