import React from 'react';
import styled from 'styled-components'
const Wrapper = styled.div`
    height:100vh;
    position:relative;
    background-size:cover;
    background-position:center;
    background-blend-mode: multiply;
    position: relative;
    // mix-blend-mode: multiply;
    // opacity: 0.2;
    .bg{
        object-fit:contain;
        opacity:.25;
        z-index:50;
    }
    .vector-bg{
        height:97%;
        z-index:100;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background-position:bottom;
        background-repeat:no-repeat;
        background-size: cover;
    }
    .content{
        position:relative;
        z-index:200;
    }
    @media (max-width:1200px){
        .vector-bg{
            background-position:bottom;
            background-size:cover;
        }
        
    }

    @media (max-width:991px){
        
    }
    @media (max-width:767px){
        
    }
    @media (max-width:500px){
        
    }
    @media (max-width:320px){
        
    }
`;

export default function index(props) {
    return (
        <Wrapper style={{backgroundImage:`linear-gradient(rgba(32, 14, 50, 0.67), rgba(32, 14, 50, 0.67)), url(${require(`../../assets/images/${props.image ? props.image : 'banner.jpg'}`)})`, height:props.half ? "50vh": "100vh"}}>
            <div style={{
                backgroundImage:`url(${require(`../../assets/images/cloud.svg`)})`,
                position:"absolute", 
                top:"0",
                right:"0",
                left:"0",
                bottom:"0"
            }} className={`bg `}>
            </div>
            <div className="content w-100 h-100">
                {props.children}

            </div>
            <div className={`vector-bg ${props.half ? 'd-none':'d-block'}`} style={{backgroundImage:` url(${require(`../../assets/images/banner-pattern.svg`)})`}}>
                
            </div>
        </Wrapper>
    )
}
